/*Bootstrap reboot*/
body {
    font-family: 'Roboto', sans-serif;
    color: #333;

    &.inner-page {
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            width: 100%;
            height: 1639px;
            background: url(../img/white-blobs.png) no-repeat 0 0;
        }
    }

    &.about-page {
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            width: 100%;
            height: 1639px;
            background: url(../img/happy-couple-blobs.png) no-repeat 0 0;
        }
    }

    &.faq-page {
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            width: 100%;
            height: 1639px;
            background: url(../img/happy-woman-blobs.png) no-repeat 0 0;
        }
    }

}

.inner-page {
    background: #fee0e0;
}

html.home {
    @media(min-width: 1200px) {
        height: 100%;
    }

    body {
        @media(min-width: 1200px) {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            min-height: 100%;
        }


        position: relative;

        &:before {
            content: '';
            position: absolute;
            z-index: -1;
            width: 100%;
            height: 100%;
            background: url(../img/violet-blobs.png) no-repeat 67% 45%;
        }

        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+0,fee0e0+100 */
        background: #ffffff;
        /* Old browsers */
        background: -moz-linear-gradient(top, #ffffff 0%, #fee0e0 100%);
        /* FF3.6-15 */
        background: -webkit-linear-gradient(top, #ffffff 0%, #fee0e0 100%);
        /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom, #ffffff 0%, #fee0e0 100%);
        /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#fee0e0', GradientType=0);
        /* IE6-9 */
    }
}

a {
    color: #432D5C;

    &:hover,
    &:focus {
        color: #6526AC;
    }
}

img {
    max-width: 100%;
}

button,
input,
textarea {
    &:focus {
        outline: none;
    }
}

.container {
    @media(min-width: 1660px) {
        max-width: 1630px;
    }
}

.table-bordered th,
.table-bordered td {
    border: 1px solid #9ea1a4;
}

.table-bordered thead th,
.table-bordered thead td {
    border-bottom-width: 1px;
}

.table thead th {
    border-bottom-color: #9ea1a4;
}

h2,
h3,
h4,
h5,
h6 {
    text-align: center;
    color: #432D5C;
    font-weight: 700;
}

h2 {
    @media(max-width: 575px) {
        font-size: 22px
    }
}

h3 {
    @media(max-width: 575px) {
        font-size: 19px;
    }
}

h4 {
    @media(max-width: 575px) {
        font-size: 17px;
    }
}

h5 {
    @media(max-width: 575px) {
        font-size: 16px;
    }
}

h6 {
    @media(max-width: 575px) {
        font-size: 15px;
    }
}

@media(min-width: 1660px) {
    .col-w-1 {
        flex: 0 0 8.33333%;
        max-width: 8.33333%;
    }

    .col-w-2 {
        flex: 0 0 16.66667%;
        max-width: 16.66667%;
    }

    .col-w-3 {
        flex: 0 0 25%;
        max-width: 25%;
    }

    .col-w-4 {
        flex: 0 0 33.33333%;
        max-width: 33.33333%;
    }

    .col-w-5 {
        flex: 0 0 41.66667%;
        max-width: 41.66667%;
    }

    .col-w-6 {
        flex: 0 0 50%;
        max-width: 50%;
    }

    .col-w-7 {
        flex: 0 0 58.33333%;
        max-width: 58.33333%;
    }

    .col-w-8 {
        flex: 0 0 66.66667%;
        max-width: 66.66667%;
    }

    .col-w-9 {
        flex: 0 0 75%;
        max-width: 75%;
    }

    .col-w-10 {
        flex: 0 0 83.33333%;
        max-width: 83.33333%;
    }

    .col-w-11 {
        flex: 0 0 91.66667%;
        max-width: 91.66667%;
    }

    .col-w-12 {
        flex: 0 0 100%;
        max-width: 100%;
    }
}

.only-w {
    @media(max-width: 1659px) {
        display: none;
    }
}

/*Header*/

.site-header__header-row {
    align-items: center;

    @media(max-width: 767px) {
        padding: 10px 0;
    }

    @media(min-width: 768px) and (max-width: 1659px) {
        padding: 20px 0;
    }

    @media(min-width: 1660px) {
        padding: 40px 0;
    }
}

.site-header__logo-col {
    @media(max-width: 767px) {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 7px;
    }
}

.site-logo {
    @media(max-width: 767px) {
        padding-right: 30px;
    }
}

a.site-logo {
    transition: opacity 0.3s ease;

    &:hover,
    &:focus {
        @media(min-width: 992px) {
            opacity: 0.75;
        }

    }

    &:active {
        @media(max-width: 991px) {
            opacity: 0.75;
        }

    }
}

.site-logo__img {
    @media(max-width: 767px) {
        max-height: 45px;
    }
}

.mobile-menu-toogle {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    border: 0;
    padding: 0;
    background: transparent;
    width: 36px;
    height: 36px;

    &:active {
        .mobile-menu-toogle__bar {
            background: #BF3882;
        }
    }

}

.mobile-menu-toogle__bar {
    height: 2px;
    width: 26px;
    background: #432D5C;
    border-radius: 3px;

    &:not(:last-child) {
        margin-bottom: 5px;
    }
}

.site-header__main-nav-col {
    @media(min-width: 768px) and (max-width: 1659px) {
        margin-top: 20px;
    }

    @media(min-width: 1660px) {
        padding-left: 95px;
    }
}

.site-header__main-navigation {
    @media(max-width: 767px) {
        margin: 0 -15px;
    }

    &>ul {
        @media(max-width: 767px) {
            margin-top: 10px;
        }

        @media(min-width: 768px) {
            display: flex;
        }
    }

    ul {
        list-style: none;
        padding-left: 0;
        margin-bottom: 0;
    }

    &>ul>li>ul {
        @media(max-width: 767px) {
            display: none;
        }

        &>li.site-header__main-navigation-subitem_active>a,
        &>li.site-header__main-navigation-subitem_active>a:hover,
        &>li.site-header__main-navigation-subitem_active>a:focus {
            @media(min-width: 576px) and (max-width: 767px) {
                color: #BF3882;
            }

            @media(min-width: 768px) {
                background: #BF3882;
                color: #fff;
                text-decoration: none;
            }

            @media(max-width: 575px) {
                background: #BF3882;
                color: #fff;
                text-decoration: none;
            }
        }

        @media(min-width: 768px) {
            padding-top: 10px;
            display: none;
            position: absolute;
            z-index: 1;
            box-shadow: 0px 12px 10px 0px rgba(50, 50, 50, 0.3);

            &>li {
                background: #fff;
            }

            &:before {
                content: '';
                position: absolute;
                z-index: -1;
                top: -3px;
                left: 15px;
                border: 7px solid transparent;
                border-bottom-color: #fff;

            }
        }

        &>li>a {
            display: block;
            padding: 10px 15px;
            transition: all 0.1s ease;

            @media(max-width: 767px) {
                padding-left: 25px;
            }

            &:hover,
            &:focus {
                @media(max-width: 767px) {
                    color: #432D5C;
                    text-decoration: none;
                }

                @media(min-width: 768px) {
                    background: #BF3882;
                    color: #fff;
                    text-decoration: none;
                }
            }

            &:active {
                @media(min-width: 576px) and (max-width: 767px) {
                    color: #BF3882;
                }

                @media(max-width: 575px) {
                    background: #BF3882;
                    color: #fff;
                    text-decoration: none
                }
            }
        }

    }

    &>ul>li.site-header__main-navigation-item_active {

        &>a,
        &>a:hover,
        &>a:focus {
            @media(min-width: 576px) and (max-width: 767px) {
                color: #BF3882;
            }

            @media(max-width: 575px) {
                background: #BF3882;
                color: #fff;
            }
        }

    }

    &>ul>li {
        @media(max-width: 767px) {
            position: relative;
        }

        @media(min-width: 768px) {
            &:hover>ul {
                display: block;
            }
        }

        &:not(:last-child) {
            @media(min-width: 768px) and (max-width: 1199px) {
                margin-right: 25px;
            }

            @media(min-width: 1200px) {
                margin-right: 50px;
            }

        }
    }

    &>ul>li>a {
        @media(max-width: 767px) {
            display: block;
            padding: 10px 15px;
        }

        font-weight: 500;
        font-size: 14px;
        text-transform: uppercase;
        color:#333;

        &:hover,
        &:focus {
            @media(max-width: 767px) {
                color: #333;
                text-decoration: none;
            }

            @media(min-width:768px) {
                color: #432D5C;
            }

        }

        &:active {
            @media(max-width: 767px) {
                color: #BF3882;
            }

            @media(max-width: 575px) {
                background: #BF3882;
                color: #fff;
            }



        }
    }
}

.site-header__main-navigation .site-header__main-navigation-item_has-cild>a {
    @media(max-width: 767px) {
        padding-right: 45px;
    }
}

.site-header__main-navigation-submenu-toggler {
    @media(min-width: 768px) {
        display: none;
    }

    @media(max-width: 767px) {
        position: absolute;
        top: 0;
        right: 0;
        width: 40px;
        height: 40px;
        background: url(../img/i/shevron-down.svg) no-repeat 50%;
        background-size: 25px auto;
    }
}

.site-header__phone-col {
    display: flex;

    @media(max-width: 767px) {
        justify-content: center;
    }

    @media(min-width: 768px) {
        justify-content: flex-end;
    }

    @media(min-width: 1660px) {
        order: 1;
    }
}

.header-phone {
    display: flex;
    align-items: center;

    &:hover,
    &:focus {
        .header-phone__text {
            color: #6526AC;
        }
    }
}

.header-phone__i {
    @media(max-width: 767px) {
        margin-right: 7px;
        max-width: 13px;
    }

    @media(min-width: 768px) {
        margin-right: 15px;
    }
}

.header-phone__text {
    @media(min-width: 768px) {
        font-size: 24px;
    }

    @media(max-width: 767px) {
        font-size: 18px;
    }

    text-transform: uppercase;
    color: #432D5C;
}

/*Footer*/
.site-footer {
    @media(max-width: 767px) {
        padding: 20px 0;
    }

    @media(min-width: 768px) and (max-width: 1659px) {
        padding: 40px 0;
    }

    @media(min-width: 1660px) {
        padding: 70px 0 55px;
    }
}

.site-footer__col {
    @media(min-width: 1200px) {
        flex: 0 0 20%;
    }

    @media(max-width: 575px) {
        &:not(:last-child) {
            margin-bottom: 30px;
        }
    }
}

.site-footer__site-footer-nav-list-title {
    font-weight: 500;
    font-size: 14px;
    text-transform: uppercase;

    @media(max-width: 575px) {
        margin-bottom: 10px;
    }

    @media(min-width: 576px) {
        margin-bottom: 20px;
    }

}

.site-footer__site-footer-nav-list {
    list-style: none;
    padding-left: 0;
    margin-bottom: 0;

    li {
        &:not(:last-child) {
            margin-bottom: 10px;
        }

        a {
            font-size: 14px;
            color: #555555;
        }
    }
}

.site-footer__stores-links-wrap {
    display: flex;
}

.site-footer__stores-links-item {
    display: flex;
    flex-direction: column;
    align-items: center;

    &:not(:last-child) {
        @media(min-width: 1660px) {
            margin-right: 50px;
        }

        @media(max-width: 1659px) {
            margin-right: 25px;
        }
    }

    &:hover,
    &:focus {
        .site-footer__stores-links-item-text {
            color: #6526AC;
        }
    }
}

.site-footer__stores-links-item-i {
    margin-bottom: 8px;
}

.site-footer__stores-links-item-text {
    font-size: 10px;
    color: #555555;
}

.site-footer__col_has-stores {
    @media(min-width: 576px) and (max-width: 991px) {
        margin-top: 30px;
    }
}

.site-footer__col_has-copy {
    @media(min-width: 576px) and (max-width: 1659px) {
        order: 1;
        margin-top: 30px;
        align-self: flex-end;
    }

    @media(min-width: 992px) and (max-width: 1659px) {
        flex: 0 0 auto;
        max-width: inherit;
        width: auto;
        margin-left: auto;
    }

    @media(min-width: 1660px) {
        display: flex;
        justify-content: center;
    }
}

.site-footer__col_self-centered {
    @media(min-width: 1660px) {
        align-self: center;
    }
}

.site-footer__copyright-text {
    font-size: 12px;
    color: #555555;
    margin-bottom: 35px;
}

.site-footer__col_has-dev-logo {
    @media(min-width: 576px) and (max-width: 1199px) {
        margin-top: 30px;
    }

    @media(min-width: 992px) and (max-width: 1659px) {
        flex: 0 0 auto;
        max-width: inherit;
        width: auto;
    }

}

.developer-logo {
    display: flex;
    flex-direction: column;

    &:hover,
    &:focus {
        .developer-logo__text {
            color: #6526AC;
        }
    }
}

.developer-logo__img {
    margin-bottom: 5px;
    max-width: 80px;
}

.developer-logo__text {
    font-size: 12px;
    color: #555555;
}

/*Pages*/
.titled-intro-section_home {
    @media(max-width: 767px) {
        margin-bottom: 30px;
    }
}

.titled-intro-section__row {
    align-items: center;
}

.titled-intro-section__title {
    font-weight: 300;

    @media(max-width: 575px) {
        font-size: 20px;
        text-align: center;
    }

