    @media(min-width: 1660px) {
        font-size: 70px;
    }

    @media(min-width: 1200px) and (max-width: 1659px) {
        font-size: 50px;
    }

    text-transform: uppercase;
    color: #432D5C;

    @media(max-width: 1199px) {
