.reviews-container__review-text {
    p:last-child {
        margin-bottom: 0;
    }

    margin-bottom: 10px;
}

.reviews-container__review-more-link {
    color: #D14F97;
    text-decoration: underline;
}

.faq-container__intro-text {
    @media(min-width: 992px) {
        margin-bottom: 60px;

        p {
            margin-bottom: 20px;
        }
    }

    @media(max-width: 991px) {
        margin-bottom: 30px;

        p {
            margin-bottom: 15px;
        }
    }

    p:last-child {
        margin-bottom: 0;
    }
}

.faq-container__question {
    position: relative;

    &:not(:last-child) {
        @media(min-width: 992px) {
            margin-bottom: 50px;
        }

        @media(max-width: 991px) {
            margin-bottom: 30px;
        }
    }

    @media(min-width: 992px) {
        padding-left: 80px;
    }

    @media(min-width: 576px) and (max-width: 991px) {
        padding-left: 60px;
    }

    &:before {
        @media(min-width: 576px) {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            background: url(../img/i/question.svg) no-repeat 0 0;
            background-size: cover;
        }

        @media(min-width: 992px) {
            width: 60px;
            height: 60px;
        }

        @media(min-width: 576px) and (max-width: 991px) {
            width: 40px;
            height: 40px;
        }
    }
}

.faq-container__question-title {
    font-weight: 500;
    font-size: 18px;
    color: #432D5C;
    margin-bottom: 10px;
}

.faq-container__question-answer {

    p:last-child,
    ul:last-child,
    ol:last-child {
        margin-bottom: 0;
    }

    ul {
        list-style: none;
        padding-left: 0;

        li {
            display: flex;

            &:before {
                content: '';
                display: block;
                flex-shrink: 0;
                width: 4px;
                height: 4px;
                margin-right: 5px;
                border-radius: 100%;
                background: #cc2d69;
                margin-top: 8px;
            }
        }
    }

    a {
        color: #BF3882;
        text-decoration: underline;

        &:hover,
        &:focus {
            color: #432D5C;
        }
    }
}

.faq-container__question-answer .faq-container__no-marker-list {
    li {
        display: block;

        &:before {
            display: none;
        }
    }
}